import { Typography } from '@mui/material';
import { Box } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import Stack from '@mui/material/Stack';
import Avatar from '@mui/material/Avatar';
import Fade from '@mui/material/Fade';
import React from 'react';
import TextTransition, { presets } from 'react-text-transition';

const TEXTS = ['Forest', 'Building', 'Tree', 'Color'];

export default function Home() {
    const theme = useTheme();
    const [index, setIndex] = React.useState(0);

    React.useEffect(() => {
      const intervalId = setInterval(
        () => setIndex((index) => index + 1),
        3000, // every 3 seconds
      );
      return () => clearTimeout(intervalId);
    }, []);


    return (
        <Box height='100vh'>
            <Stack spacing={2} margin="auto">
                <Box>
                    <Avatar alt="Daniel Tok" src="/src/assets/profilePic.jpeg"  sx={{ width: 150, height: 150, margin: 'auto' }}/>
                </Box>
                <Typography color={theme.palette.primary.light} variant='h3' align='center'>
                    <b>DANIEL TOK</b>
                </Typography>
                <Typography color={theme.palette.primary.main} variant='h6' align='center'>
                    Senior @ University of Michigan - Ann Arbor | ITS Network Infrastructure Intern | B.S.E Computer Science
                </Typography>
                <TextTransition springConfig={presets.wobbly}>{TEXTS[index % TEXTS.length]}</TextTransition>
            
            </Stack>
      
        </Box>
       
    )
}